import React from "react";
import { ReactSVG } from 'react-svg';


export type iconProps=
{
imageSrc:string;
classname?:string
}

const Icon = (props:iconProps)=> {

    const { imageSrc, classname }= props

    return(
        <ReactSVG src={imageSrc} className={classname}/>
    )
}

export default Icon;